<template>
  <form @submit.prevent="updateEgr" >
            <div>

                <input
                    type='text'
                    name='descripcion'
                    class='form-control mt-3'
                    placeholder='Descripcion'
                    autoComplete='off'
                    v-model="editEgreso.descripcion"

                />
                 <input
                    type='number'
                    name='monto'
                    class='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    v-model="editEgreso.monto"

                />
                

                <button 
                class='btn btn-primary form-control  mt-2'
                type='submit'  
                
                >
                Actualizar

                </button>

            </div>
        </form>
        <button class="btn btn-success mt-5" @click="salirEdit" >Salir</button>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
export default {
    data(){
        return{
            editEgreso:{}
        }
    },
    mounted(){
        this.editEgreso = this.egreso
    },
    computed:{
        ...mapState('egreso',['egreso']),
    },
    methods:{
     ...mapActions('egreso',['updateEgreso']),
     ...mapMutations('egreso',['salirEdit']),

     async updateEgr() {
            
            await this.updateEgreso(this.editEgreso)
       }
    }
}
</script>

